import React from 'react';
import {Grid, Cell}  from 'react-mdl';

function Land(){
    return(
        <div style={{width:'100%', margin:'auto'}}>
            <Grid className="land-grid">
                <Cell col={12}>
                    <img 
                      src="https://aboullaite.me/content/images/2016/11/myAvatar.svg"
                      alt="Default"
                      className="avatar-img"
                    />

                    <div className="banner-text">
                        <h1>Full Stack Web Developer</h1>

                        <hr/>

                        <p>HTML/CSS | JS | Django | React | React Native | NodeJS | MongoDB | Express | Bootstrap</p>

                        <div className="social-links">
                        {/*Linked in*/}
                        <a href="https://www.google.com" rel="noopener noreferrer" target="_blank">
                            <i className="fa fa-linkedin-square" aria-hidden="true"/>
                        </a>
                        {/*GitHub*/}
                        <a href="https://github.com/Abderahmane230" rel="noopener noreferrer" target="_blank">
                            <i className="fa fa-github-square" aria-hidden="true"/>
                        </a>
                        {/*FaceBook*/}
                        <a href="https://www.facebook.com/abde.rahmane.79219" rel="noopener noreferrer" target="_blank">
                            <i className="fa fa-facebook-square" aria-hidden="true"/>
                        </a>
                    </div>
                    </div>
                </Cell>
            </Grid>
        </div>
    );
}

export default Land;