import React from 'react';
import {Tabs, Tab, Grid, Cell, Card, CardTitle,CardText, CardActions, Button, CardMenu, IconButton} from 'react-mdl';

class Projects extends React.Component{
    constructor(props) {
        super(props);
        this.state = {activeTab:0};
    }
    toggleCategories(){
        if(this.state.activeTab === 0){
            return(
                <Card shadow={5} style={{minWidth:'450', margin:'auto'}}>
                    <CardTitle style={{color:'#fff', height:'175px', background:'url(https://cdn.worldvectorlogo.com/logos/react-1.svg) center / cover'}}>
                        React project #1
                    </CardTitle>
                    <CardText>
                        This demo right now, soon i will drop my pesonals works on react .
                    </CardText>
                    <CardActions border>
                        <Button colored>GitHub</Button>
                        {/*<Button colored>CodePen</Button>*/}
                        {/*<Button colored>LiveDemo</Button>*/}
                    </CardActions>
                    <CardMenu style={{color:'#fff'}}>
                        <IconButton name="share"/>
                    </CardMenu>
                </Card>
            );
        }else if(this.state.activeTab === 1){
            return(
                <Card shadow={5} style={{minWidth:'450', margin:'auto'}}>
                <CardTitle style={{color:'#fff', height:'175px', background:'url(https://themeforest.img.customer.envatousercontent.com/files/180904265/djangopre.jpg?auto=compress%2Cformat&q=80&fit=crop&crop=top&max-h=8000&max-w=590&s=9625d406063207f9205a65eb7d1c6ff0) center / cover'}}>
                    Django project #1
                </CardTitle>
                <CardText>
                    This demo right now, soon i will drop my pesonals works on react .
                </CardText>
                <CardActions border>
                    <Button colored>GitHub</Button>
                    {/*<Button colored>CodePen</Button>*/}
                    {/*<Button colored>LiveDemo</Button>*/}
                </CardActions>
                <CardMenu style={{color:'#fff'}}>
                    <IconButton name="share"/>
                </CardMenu>
            </Card>
            );
        }else if(this.state.activeTab === 2){
            return(
                <Card shadow={5} style={{minWidth:'450', margin:'auto'}}>
                <CardTitle style={{color:'#fff', height:'175px', background:'url(https://miro.medium.com/max/900/0*UqGyYmWCRQnjLzSk.jpg) center / cover'}}>
                    Mern stack project #1
                </CardTitle>
                <CardText>
                    This demo right now, soon i will drop my pesonals works on react .
                </CardText>
                <CardActions border>
                    <Button colored>GitHub</Button>
                    {/*<Button colored>CodePen</Button>*/}
                    {/*<Button colored>LiveDemo</Button>*/}
                </CardActions>
                <CardMenu style={{color:'#fff'}}>
                    <IconButton name="share"/>
                </CardMenu>
            </Card>
            );
        }else if(this.state.activeTab === 3){
            return(
                <Card shadow={5} style={{minWidth:'450', margin:'auto'}}>
                <CardTitle style={{color:'#fff', height:'175px', background:'url(https://www.itrust.fr/wp-content/uploads/2017/01/mongodb-retina-preview.png) center / cover'}}>
                    MongoDB project #1
                </CardTitle>
                <CardText>
                    This demo right now, soon i will drop my pesonals works on react .
                </CardText>
                <CardActions border>
                    <Button colored>GitHub</Button>
                    {/*<Button colored>CodePen</Button>*/}
                    {/*<Button colored>LiveDemo</Button>*/}
                </CardActions>
                <CardMenu style={{color:'#fff'}}>
                    <IconButton name="share"/>
                </CardMenu>
            </Card>
            );
        }
    } 

    render(){
        return(
        <div className="category-tabs">
            <Tabs activeTab = {this.state.activeTab}onChange = {(tabId) => this.setState({activeTab: tabId})}>
                <Tab>React</Tab>
                <Tab>Django</Tab>
                <Tab>Mern</Tab>
                <Tab>MongoDB</Tab>
            </Tabs>

            <section className="projects-grid">
                <Grid className="projects-grid">
                    <Cell col={12}>
                        <div className="content">
                            {this.toggleCategories()}
                        </div>
                    </Cell>
                </Grid>
            </section>
        </div>
        );
    }
}

export default Projects;